.roadmap__first-stage {
  position: relative;
}

.roadmap__first-stage::before {
  content: '';
  display: block;
  width: 328px;
  height: 1px;
  background: #1DBE6A;
  box-shadow: 0px 0px 69px #1DBE6A, 0px 4px 164px #1DBE6A;
  position: absolute;
  left: -236px;
  /* a half of the circle */
  top: 12px;
  overflow: hidden;
}

@media screen and (max-width: 769px) {
  .roadmap__first-stage::before {
    display: none;
  }
}
