.dropdown__button {
  position: relative;
  padding: 14px 80px 14px 30px;
}

.dropdown__button::after {
  content: '';
  position: absolute;
  background-image: url('/src/images/arrow.svg');
  width: 16px;
  height: 9px;
  top: 50%;
  right: 25px;
  transform: translateY(-50%) rotateZ(0);
  opacity: .5;
  transition: transform .25s ease;
}
