.footer__column>a {
  display: block;
  margin-top: 16px;
}

.footer__column:not(:first-child) {
  margin-left: 30px;
}

.footer__column:last-child {
  margin-left: auto;
  margin-right: 0;
}

@media screen and (max-width: 769px) {
  .footer__column {
    flex-direction: column;
    padding-bottom: 50px;
  }
}
