.sale-stages {
  max-width: 354px;
  width: 100%;
  background-repeat: no-repeat;
  border-radius: 60px;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  align-items: stretch;
  overflow: hidden;
  border: 3px solid #676766;
  position: relative;
}

.sale-stages::before {
  content: '';
  background-image: url('/src/images/bubbles.svg');
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
  opacity: .4;
}

.sale-stages p {
  z-index: 1;
  text-align: center;
  vertical-align: middle;
}

.sale-stages>div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.sale-stages__pre-sale {
  padding: 10px;
  background: linear-gradient(180deg, #00DE6A 0%, #005428 100%);
  width: 30%;
}

.sale-stages__public-sale {
  padding: 10px;
  background: linear-gradient(180deg, #EBA844 0%, #E19A2F 0.01%, #8E5F17 100%);
  width: 50%;
}

.sale-stages__airdrop {
  padding: 10px;
  background: linear-gradient(180deg, #EB4444 0%, #7A1E1E 100%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  width: 30%;
}

@media screen and (max-width: 769px) {
  .sale-stages {
    max-width: 100%;
  }
}
