.section_background_gradient {
  background: linear-gradient(180deg, #2DE083 0%, #007A3A 100%);
}

.section_background_gradient::after {
  content: '';
  background-color: black;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: .8;
  z-index: 0;
}

.section_background_gradient > * {
  z-index: 1;
}
