.header {
  padding: 18px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: var(--header-height);
}

.header>*:nth-child(4) {
  margin-right: 60px;
  margin-left: 30px;
}

.header>*:nth-child(3) {
  margin-right: 0;
  margin-left: 0;
  flex-basis: 45%;
}

.header>*:nth-child(2) {
  margin-right: 10px;
  margin-left: 30px;
}

