.search {
  background-color: transparent;
  width: 100%;
  border: 2px rgba(255, 255, 255, .2) solid;
  border-radius: var(--border-radius);
  display: block;
  max-height: 48px;
  box-sizing: border-box;
  position: relative;
  transition: var(--transition);
}

.search:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.search::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 20px;
  background-image: url('/src/images/search.svg');
  width: 24px;
  height: 24px;
  display: block;
  opacity: .15;
}

.search:hover::before {
  opacity: .5;
}

.search:active::before {
  opacity: 1;
}
