.rarity-count-bar {
  position: relative;
  width: 100%;
  min-width: 80px;
}

.rarity-count-bar .rarity-count-bar__count-bar {
  height: 20px;
  margin-left: 27px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 8px;
}

.increase-width {
  animation: forwards increaseWidth 2s;
}

.rarity-count-bar .text {
  text-transform: uppercase;
}

.rarity-count-bar__badge {
  text-transform: uppercase;
  max-width: 50px;
  position: absolute;
  top: -50%;
  z-index: 1;
}

.rarity-count-bar__level {
  font-weight: 700;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -60%);
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
}

.rarity-count-bar_color_yellow .rarity-count-bar__level {
  color: #906B0A;
}

.rarity-count-bar_color_violet .rarity-count-bar__level {
  color: #411C7E;
}

.rarity-count-bar_color_cyan .rarity-count-bar__level {
  color: #207894;
}

.rarity-count-bar_color_yellow .rarity-count-bar__count-bar {
  background: right center / 80% 100% radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), #B3881C;
}

.rarity-count-bar_color_violet .rarity-count-bar__count-bar {
  background: right center / 80% radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), #5E1CB3
}

.rarity-count-bar_color_cyan .rarity-count-bar__count-bar {
  background: right center / 80% radial-gradient(100% 100% at 50% 0%, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0) 100%), #1C85B3;
}

@keyframes increaseWidth {
  0% {
    opacity: 0;
    width: 0;
  }
  100% {
    opacity: 1;
    width: 100%;
  }
}
