.dropdown {
  position: relative;
}

.dropdown select {
  display: none;
}

.dropdown_active .dropdown__list {
  display: flex;
}

.dropdown_active .dropdown__button::after {
  transform: translateY(-50%) rotateZ(180deg);
}

.dropdown__button {
  position: relative;
  padding: 14px 80px 14px 30px;
}

.dropdown__button::after {
  content: '';
  position: absolute;
  background-image: url(/static/media/arrow.3a7df3f6.svg);
  width: 16px;
  height: 9px;
  top: 50%;
  right: 25px;
  transform: translateY(-50%) rotateZ(0);
  opacity: .5;
  transition: transform .25s ease;
}

.dropdown__list {
  background-color: rgba(26, 29, 27, 0.9);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  -webkit-backdrop-filter: blur(8px);
          backdrop-filter: blur(8px);
