.section {
  display: flex;
  flex-direction: column;
  position: relative;
}

/* https://habr.com/ru/post/463923/  */

@media screen and (min-width: 1920px) {
  .section {
    padding: 0 calc((100% - 1380px) / 2);
  }
}

@media screen and (max-width: 1920px) {
  .section {
    padding: 0 calc((100% - 80vw) / 2);
  }
}

