.button_primary {
  font-weight: bold;
  background: linear-gradient(180deg, #00FF7A 0%, #00C25D 100%);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1), inset 0px -4px 4px rgba(0, 0, 0, 0.25), inset 0px 4px 4px rgba(255, 255, 255, 0.25);
}

.button_primary:hover {
  box-shadow: 0 4px 12px rgb(0 0 0 / 10%), inset 0 -4px 40px 8px rgb(19 20 19 / 90%);
}
