.top-artists__row {
  display: flex;
  justify-content: space-between;
  margin-top: 22px;
}

@media screen and (max-width: 769px) {
  .top-artists__row:first-of-type {
    margin-top: 30px;
  }

  .top-artists__row:last-child {
    display: none;
  }
  .top-artists__row {
    flex-direction: column;
    margin-top: 0;
  }
}
