.subscribe {
  background-color: transparent;
  max-width: 316px;
  width: 100%;
  border: 2px rgba(255, 255, 255, .2) solid;
  border-radius: var(--border-radius);
  display: flex;
  max-height: 48px;
  box-sizing: border-box;
  position: relative;
  transition: var(--transition);
  padding: 2px;
}

.subscribe input {
  background-color: transparent;
  height: 100%;
  max-width: 100%;
  padding: 14px 18px;
  width: 100%;
  display: block;
  border: 0;
  box-sizing: border-box;
  outline: 0;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

.subscribe input::placeholder {
  color: white;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  opacity: .25;
}

.subscribe input:hover::placeholder {
  opacity: .5;
}

.subscribe input:active::placeholder {
  opacity: 1;
}

.subscribe .button {
  padding: 11px 21px;
}
