.stage_active .stage__circle {
  background-color: #1DBE6A;
  box-shadow: 0px 0px 69px #1DBE6A, 0px 4px 164px #1DBE6A;
}

.stage_active .stage__circle::before, .stage_active .stage__circle::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  transform: translateY(-50%) translateX(-50%);
  background-color: #1DBE6A;
}

.stage_active .stage__circle::before {
  width: 64px;
  height: 64px;
  opacity: .05;
}

.stage_active .stage__circle::after {
  width: 128px;
  height: 128px;
  opacity: .02;
}

.stage_active .stage__title {
  color: #1DBE6A;
  opacity: 1;
  text-shadow: 0px 0px 69px #1DBE6A, 0px 4px 164px #1DBE6A;
}

.stage_active .stage__description {
  text-shadow: 0px 0px 69px #1DBE6A, 0px 4px 164px #1DBE6A;
  color: #1DBE6A;
  opacity: 1;
}
