.top-artists {
  background-color: #131413;
  padding-top: 100px;
  padding-bottom: 150px;
  position: relative;
  overflow: hidden;
}

.top-artists::before, .top-artists::after {
  content: '';
  background-image: url('/src/images/caution.svg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: block;
  width: 2635px;
  height: 76px;
  left: 0;
  top: 0;
  position: absolute;
  transform: rotate(-15.24deg) translate(-20%, 200%);
}

.top-artists::after {
  transform: rotate(30deg);
  left: -10%;
  top: auto;
  bottom: 0;
  display: none;
}

@media screen and (max-width: 769px) {
  .top-artists::before {
    transform: rotate(-30deg) translate(-10%, -500%);
  }
  .top-artists::after {
    display: block;
    transform: rotate(30deg) translate(-30%, 500%);
  }
}
