.announce {
  background-color: #1E1E1E;
  padding-top: 100px;
  padding-bottom: 100px;
}

.announce__row {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;
  margin-left: -10px;
  margin-right: -10px;
}

@media screen and (max-width: 769px) {
  .announce__row {
    margin-top: 0;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  .announce__row:last-child {
    display: none;
  }
  .announce .heading {
    text-align: center;
  }
  .announce__row>.announce-card {
    margin: 10px;
  }
}
