.search__input {
  background-color: transparent;
  height: 100%;
  max-width: 100%;
  padding: 14px 20px 14px 64px;
  width: 100%;
  display: block;
  border: 0;
  box-sizing: border-box;
  outline: 0;
  color: white;
  font-family: 'Montserrat', sans-serif;
}

.search__input::placeholder {
  color: white;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  opacity: .25;
}

.search__input:hover::placeholder {
  opacity: .5;
}

.search__input:active::placeholder {
  opacity: 1;
}
