.main-section__bg {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  left: 0;
  top: 0;
}

.main-section__bg>img {
  object-fit: cover;
  object-position: center;
  filter: blur(14px);
  margin: -14px;
  transform: translate(-20%, -50%);
}

.main-section__bg::before {
  content: '';
  background-color: rgba(0, 0, 0, .55);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 0;
}
