.rarity {
  background-color: #0F1110;
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  overflow-x: hidden;
}

.rarity>.rarity-count-bar {
  margin-top: 48px;
}

.rarity__header {
  text-align: right;
  position: absolute;
  padding: inherit;
  padding-top: 0;
  padding-bottom: 0;
  right: 0;
}

.rarity__header .text {
  margin-top: 24px;
  line-height: 1.5;
}

.rarity__pentagon {
  position: absolute;
}

.rarity__pentagon_violet {
  padding: inherit;
  margin-right: -200px;
  right: 0;
}

.rarity__pentagon_yellow {
  left: 0;
  bottom: 100px;
}

.rarity__pentagon_cyan {
  padding: inherit;
  margin-right: -150px;
  right: 0;
  bottom: 0;
}

@media screen and (max-width: 1590px) {
  .rarity__header {
    position: relative;
  }
}

@media screen and (max-width: 1320px) {
  .rarity__header {
    text-align: left;
  }
  .rarity__pentagon_violet {
    margin-right: 0;
    padding: 0;
    top: 40%;
  }
  .rarity__pentagon_cyan {
    margin-right: 0;
    padding: 0;
    top: 30px;
    bottom: auto;
  }
  .rarity__pentagon_yellow {
    display: none;
  }
}
