.dropdown__list {
  background-color: rgba(26, 29, 27, 0.9);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(8px);
  border-radius: 24px;
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 10px;
  position: absolute;
  width: 100%;
  box-sizing: border-box;
}
