.announce-card {
  max-width: 316px;
  overflow: hidden;
  position: relative;
  border-radius: 14px;
  margin: 0 10px;
}

.announce-card::before {
  content: '';
  display: block;
  width: 100px;
  height: 100px;
  background-image: url('/src/images/user.svg');
  position: absolute;
  left: 50%;
  top: 88px;
  transform: translate(-50%, 0);
  z-index: 1;
}

.announce-card__image {
  width: 316px;
  height: 316px;
  filter: blur(50px);
}

.announce-card__info {
  position: absolute;
  bottom: 20px;
  left: 20px;
  display: flex;
  align-items: center;
}

.announce-card__info>div {
  margin-left: 16px;
}

.announce-card__info .text {
  margin-top: 5px;
}

.announce-card__avatar {
  width: 58px;
  height: 58px;
  object-fit: cover;
  background-color: rgba(255, 255, 255, .3);
  border-radius: 18px;
}
